import React from 'react';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
// lazysizes Image
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';
// Styles-module
import picture from './picture.module.scss';
// Icon
import lazyImg from './lazy-img.svg';
// Types
interface IPictureProps {
  src: string;
  alt: string;
  cssClasses?: { box?: string | string[]; img?: string | string[] };
  lazyload?: boolean;
}

const Picture: React.FC<IPictureProps> = ({
  src,
  alt,
  cssClasses,
  lazyload = true,
}) => {
  // Styles-module
  const stImage = convertInString(
    picture.img,
    cssClasses?.img,
    lazyload && 'lazyload'
  );
  // Path Image
  const srcSource = {
    ...(lazyload ? { srcSet: lazyImg, 'data-srcset': src } : { srcSet: src }),
  };
  const srcImg = {
    ...(lazyload ? { src: lazyImg, 'data-src': src } : { src }),
  };

  return (
    <picture className={convertInString(picture.img__box, cssClasses?.box)}>
      <source className={stImage} {...srcSource} />
      <img className={stImage} {...srcImg} alt={alt} />
    </picture>
  );
};

export default Picture;
