import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Components
import Icon from '../../Common/Icon';

const BtnLanguage: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <button
      className="btn-language active-color-1"
      onClick={() => lang.setValue(lang.code === 'ru' ? 'en' : 'ru')}
      aria-label={lang.bool ? 'Язык' : 'Language'}
    >
      {lang.bool ? 'Русский' : 'English'}
      <Icon active={lang.code} size={{ width: 75, height: 50 }} />
    </button>
  );
};

export default BtnLanguage;
