import React, { useContext, useEffect, useRef } from 'react';
// React Context
import { sectionContext } from '../../context/sectionContext';
// Utils
import { convertInString } from '../../utils/js/сonvert';
import { activeColor } from '../../utils/ts/activeColor';
// Components
import Icon from '../Common/Icon';
// Styles-module
import messageStatus from './messageStatus.module.scss';
// Types
interface IMessageStatusProps {
  status: boolean;
}

const MessageStatus: React.FC<IMessageStatusProps> = ({ status }) => {
  // React Context
  const { lang } = useContext(sectionContext);
  // React Ref
  const messageRef = useRef<HTMLParagraphElement>(null);
  // React Effect
  useEffect(() => {
    if (status)
      setTimeout(
        () => messageRef.current?.classList.add(messageStatus.main__hidden),
        1000
      );
  }, [status, messageRef]);

  return (
    <p
      ref={messageRef}
      className={convertInString(
        messageStatus.main,
        ...activeColor('background-1', 'boxShadow-2', 'color-2')
      )}
    >
      <Icon
        active={status ? 'online' : 'offline'}
        size={{ width: 65, height: 50 }}
      />
      {status
        ? lang.bool
          ? 'Соединение восстановлено'
          : 'Connection restored'
        : lang.bool
        ? 'Нет доступа к интернету'
        : 'No internet access'}
    </p>
  );
};

export default MessageStatus;
