import React, { useState, useRef, useEffect, useContext } from 'react';
// Router
import { useLocation, useNavigate } from 'react-router-dom';
// React Context
import { sectionContext } from '../../context/sectionContext';
// Components
import Logo from './Logo';
import Copyright from './Copyright';
import IconBurger from '../Common/Icon/IconBurger';
import Icon from '../Common/Icon';
// Styles-module
import header from './header.module.scss';
import style from './styles';

const Header: React.FC = () => {
  // Router
  const navigate = useNavigate();
  const parameterSearch = useLocation().search;
  // React Context
  const {
    screenWidth,
    lang,
    location: { path, paths },
    sections,
    animateTime,
  } = useContext(sectionContext);
  const screenSize = screenWidth <= 800;
  // React State
  const [burger, setBurger] = useState<boolean>(true);
  // React Ref
  const headerRef = useRef<HTMLElement>(null);
  const headerModalRef = useRef<HTMLDivElement>(null);
  const btnMenuRef = useRef<HTMLButtonElement>(null);
  // Ref elements
  const elHeader = headerRef.current;
  const elModal = headerModalRef.current;
  const elBtnMenu = btnMenuRef.current;
  // React Effect
  useEffect(() => {
    const timer = screenSize
      ? setInterval(() => setBurger(!burger), animateTime.init * 5)
      : 1;

    const handleClick = (e: MouseEvent) => {
      const checkEl = (el: Node | null): boolean =>
        !el?.contains(e.target as Node);

      if (checkEl(elHeader) && checkEl(elBtnMenu)) return closeMenu();
    };

    if (screenSize) document.body.addEventListener('click', handleClick);

    return () => {
      clearInterval(timer as number);
      document.body.removeEventListener('click', handleClick);
    };
  });
  // Visible button menu and hidden menu
  const closeMenu = () => {
    elBtnMenu?.classList.remove(header['main__btn-hidden']);
    elHeader?.classList.add(header.main__close);
  };
  // Following a link
  const onClickLink = (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    const href = `?${new URL(e.currentTarget.href).searchParams.toString()}`;

    if (parameterSearch !== href)
      if (screenSize) {
        navigate(`/${href}`);
        setTimeout(() => screenSize && closeMenu(), animateTime.init - 300);
      } else {
        elModal?.classList.add(header.modal__active);
        setTimeout(() => (document.body.style.overflowY = 'hidden'), 400);

        setTimeout(() => {
          navigate(`/${href}`);
          setTimeout(() => {
            elModal?.classList.remove(header.modal__active);
            setTimeout(() => (document.body.style.overflowY = 'auto'), 400);
          }, 200);
        }, animateTime.init + 10);
      }
  };

  return (
    <>
      {screenSize && (
        <button
          ref={btnMenuRef}
          className={style.btnBurger}
          onClick={(e) => {
            e.currentTarget.classList.toggle(header['main__btn-hidden']);
            elHeader?.classList.toggle(header.main__close);
          }}
        >
          <IconBurger
            cssClasses={{
              main: style.burger(burger),
              topLine: header['main__btn-burger-top'],
              bottomLine: header['main__btn-burger-bottom'],
            }}
            animateTime={(animateTime.init * 5) / 1000}
          />
          {lang.bool ? 'Навигация' : 'Navigation'}
        </button>
      )}
      <header ref={headerRef} className={style.main(screenSize)}>
        <nav className={header.nav}>
          <Logo />
          <ul className={header.list}>
            {paths.map((href, i) => (
              <li key={`nav-link-${i + 1}`} className={header.item}>
                <a
                  href={`${process.env.REACT_APP__PARAMETER_SEARCH}${href}`}
                  className={style.link(href, path, screenSize)}
                  onClick={onClickLink}
                  onMouseLeave={(e) => e.currentTarget.blur()}
                >
                  <Icon
                    active={href}
                    size={50}
                    cssClass={{ backgroundColor: 'active-fill-1' }}
                  />
                  <span
                    className={style.linkDesc}
                    children={sections[href][lang.code]}
                  />
                </a>
              </li>
            ))}
          </ul>
        </nav>
        {!screenSize && <Copyright />}
        {!screenSize && (
          <div
            ref={headerModalRef}
            className={style.modal}
            style={{
              transitionDuration: `${animateTime.init / 1000}s`,
              animationDuration: `${animateTime.init / 1000}s`,
            }}
          />
        )}
      </header>
    </>
  );
};

export default Header;
