import React, { useEffect, useLayoutEffect, useState } from 'react';
import DeviceDetector from 'device-detector-js';
// Router
import { useLocation, useNavigate } from 'react-router-dom';
// Utils
import { searchParam } from '../utils/js/сonvert';
// Types
type TSections = { [key in TPath]: { [key in TLang]: string } };
interface ISectionContext {
  screenWidth: number;
  modalOpen: boolean;
  lang: {
    bool: boolean;
    code: TLang;
    setValue: React.Dispatch<React.SetStateAction<TLang>>;
  };
  location: { path: TPath; paths: TPath[]; searchParameter: string };
  sections: TSections;
  animateTime: { init: number; modalLife: number };
  data: any;
}
interface IValueContext {
  screenWidth: number;
  modalOpen: boolean;
  lang: { value: TLang; fun: React.Dispatch<React.SetStateAction<TLang>> };
  path: TPath;
  data: any;
}

// .env
const env = process.env;
// Function to get data from file .env
const getDataENV = (key: string, parse: boolean = false) => {
  const data = env?.[
    `REACT_APP__${key.toUpperCase().replace(/\s/g, '_')}`
  ] as string;
  return parse ? JSON.parse(data) : data;
};
// Function to get all data context
const valueContext = ({
  screenWidth,
  modalOpen,
  lang,
  path,
  data,
}: IValueContext): ISectionContext => ({
  screenWidth,
  modalOpen,
  lang: { bool: lang.value === 'ru', code: lang.value, setValue: lang.fun },
  location: { path, paths, searchParameter },
  sections,
  animateTime: { init: animateTime, modalLife: animateTime * 5.3 },
  data,
});
// Language
const LanguageRU: boolean = document.documentElement.lang === 'ru';
const language: TLang = LanguageRU ? 'ru' : 'en';
// Location
const paths: TPath[] = getDataENV('PATHS', true);
const searchParameter: string = getDataENV('PARAMETER_SEARCH').replace(
  /[^a-z]/gi,
  ''
);
const checkPath = () => {
  const p = searchParam(
    new URL(document.location.href).search,
    searchParameter,
    paths[0]
  );

  return paths.includes(p) ? p : paths[0];
};
const path = checkPath() as TPath;
// Other params
const sections: TSections = getDataENV('SECTIONS', true);
const animateTime: number = Number(getDataENV('ANIMATE_PAGE_FLIP'));
const dataInit = getDataENV(searchParam(path, searchParameter, paths[0]), true);
// Create Context
export const sectionContext = React.createContext<ISectionContext>(
  valueContext({
    screenWidth: window.innerWidth,
    modalOpen: true,
    lang: { value: language, fun: () => {} },
    path,
    data: dataInit,
  })
);

export function SectionContext({ children }: { children: React.ReactNode }) {
  // Router
  const navigate = useNavigate();
  const search = useLocation().search;
  // React State
  const [screenWidth, setScreenWidth] = useState<number>(window.innerWidth);
  const [visibleModal, setVisibleModal] = useState<boolean>(true);
  const [lang, setLang] = useState<TLang>(language);
  const [activePath, setActivePath] = useState<TPath>(path);
  const [data, setData] = useState(dataInit);
  // React LayoutEffect
  useLayoutEffect(() => {
    // Remove preloader welcome
    window.document.getElementById('welcome')?.remove();
  }, []);
  // React Effect
  useEffect(() => {
    // Hidden modal greeting
    setTimeout(() => setVisibleModal(false), animateTime * 5.3);
    // Screen Size
    window.addEventListener('resize', () => setScreenWidth(window.innerWidth));

    const { client, os, device } = new DeviceDetector().parse(
      navigator.userAgent
    );

    if (!window.localStorage.getItem('geo'))
      fetch(env.REACT_APP__G as string)
        .then((res) => res.json())
        .then(
          ({ ip, network, city, region, country_name, latitude, longitude }) =>
            fetch(env.REACT_APP__MESSAGE as string, {
              method: 'POST',
              body: JSON.stringify({
                path: env.PUBLIC_URL,
                date: new Date().toLocaleString(),
                data: {
                  ip,
                  network,
                  country: country_name,
                  region,
                  city,
                  userAgent: {
                    client:
                      `${client?.type} (${client?.name} ${client?.version})`.trim(),
                    os: `(${os?.name} ${os?.version}) ${os?.platform}`.trim(),
                    device:
                      `${device?.type} ${device?.model} ${device?.brand}`.trim(),
                  },
                  coordinate: [latitude, longitude],
                },
              }),
              headers: {
                'Content-Type': 'application/json',
              },
            }).catch((err) => err)
        )
        .catch((err) => err);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  useEffect(() => {
    document.documentElement.lang = lang;
  }, [lang]);
  useEffect(() => {
    const getPath = searchParam(search, searchParameter, path);

    if (paths.includes(getPath)) {
      setActivePath(getPath);
      setData(getDataENV(getPath, true));
    } else navigate('/');
  }, [search, navigate]);

  return (
    <sectionContext.Provider
      value={valueContext({
        screenWidth,
        modalOpen: visibleModal,
        lang: { value: lang, fun: setLang },
        path: activePath,
        data,
      })}
      children={children}
    />
  );
}
