import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
import { activeColor } from '../../../utils/ts/activeColor';
// Components
import IconLogo from '../../Common/Icon/IconLogo';
// Styles-module
import logo from './logo.module.scss';
const logoBox = convertInString(
  logo.logo__box,
  ...activeColor('borderColor-1', 'background-2', 'boxShadow-2')
);
const logoDesc = convertInString(logo['logo__desc'], 'active-color-effect');

const Logo: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <div className={logoBox}>
      <IconLogo
        backgroundColor={{
          1: 'active-stopColor-3',
          2: 'active-stopColor-2',
        }}
        iconColor="#38495a"
      />
      <p
        className={logoDesc}
        style={{
          fontSize: lang.bool ? '0.6rem' : '0.8rem',
        }}
      >
        {lang.bool
          ? 'Галактика нуждается в креативном разработчике...'
          : 'The galaxy needs a creative developer...'}
      </p>
    </div>
  );
};

export default Logo;
