import React, { useContext, useEffect, useState } from 'react';
// Router
import { Route, Routes } from 'react-router-dom';
// React Context
import { sectionContext } from './context/sectionContext';
// Hooks
import { useStatusOnline } from './hooks/useStatusOnline';
// Components
import Header from './components/Header';
import Greeting from './components/Greeting';
import Main from './components/Main';
import NotFound from './components/NotFound';
import MessageStatus from './components/MessageStatus';
import { BtnLanguage, BtnCV, BtnScroll } from './components/BtnControl';

const App: React.FC = () => {
  // React Context
  const { location, modalOpen } = useContext(sectionContext);
  // React Change Status
  const [onLine] = useStatusOnline();
  // React State
  const [visibleStatus, setVisibleStatus] = useState<boolean>(false);
  // React Effect
  useEffect(() => {
    if (!onLine) setVisibleStatus(true);
    else setTimeout(() => setVisibleStatus(false), 1795);
  }, [onLine]);

  return (
    <>
      {modalOpen && <Greeting />}
      <Header />
      <Routes>
        <Route path="/" element={<Main />} />
        <Route path="*" element={<NotFound />} />
      </Routes>
      <div className="box__languageCV">
        <BtnLanguage />
        <BtnCV />
      </div>
      <BtnScroll />
      {visibleStatus && <MessageStatus status={onLine} />}
      {location.path !== 'about' && (
        <>
          <div className="space-background" />
          <div className="stars" />
          <div className="twinkling" />
        </>
      )}
    </>
  );
};

export default App;
