import React, { useContext } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
// Styles-module
import loading from './loading.module.scss';

const Loading: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);

  return (
    <p
      className={convertInString(loading.main, 'active-color-effect')}
      children={`${
        lang.bool ? 'Подождите, идёт загрузка' : 'Please wait, loading'
      }...`}
    />
  );
};

export default Loading;
