import React, { useContext, useEffect } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Components
import Picture from '../../Common/Picture';
// Styles-module
import about from './about.module.scss';
// Assets
import { image } from '../../../assets';

const About: React.FC = () => {
  let countAnimate: number = 0;
  // React Context
  const { lang, data } = useContext(sectionContext);
  // React Context
  const { modalOpen, animateTime } = useContext(sectionContext);
  // React Effect
  useEffect(() => {
    !modalOpen &&
      setTimeout(
        () => paragraphActive(1, about.desc__active),
        animateTime.init - 200
      );
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [modalOpen]);
  // Add active css class paragraph
  const paragraphActive = (numder: number, cssClass: string) =>
    document.getElementById(`paragraph-${numder}`)?.classList.add(cssClass);
  // Search last child array string
  const lastChild = (array: string[]) =>
    array.lastIndexOf(array.at(-1) as string);

  return (
    <>
      <div className={about.container}>
        <Picture
          alt="My photo"
          src={image.about}
          cssClasses={{ img: about.img }}
          lazyload={false}
        />
        <div className={about.desc__box}>
          {(data[lang.code] as DataAbout).map((desc, index) => (
            <p
              key={`about-paragraph-${index + 1}`}
              id={`paragraph-${index + 1}`}
              className={about.desc}
              dangerouslySetInnerHTML={{
                __html: desc
                  .split('')
                  .map((el, i, arr) => {
                    if (!modalOpen && lastChild(arr) === i)
                      setTimeout(
                        () => paragraphActive(index + 2, about.desc__active),
                        (countAnimate += i * 10 + animateTime.init)
                      );

                    return `<span class="active-color-1" style="transition-delay: ${
                      i / 100
                    }s">${el}</span>`;
                  })
                  .join(''),
              }}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default React.memo(About);
