// Utils
import { convertInString } from '../../utils/js/сonvert';
import { activeColor } from '../../utils/ts/activeColor';
// Styles-module
import header from './header.module.scss';

const modal = convertInString(header.modal, 'active-gradient');
const main = (screenSize: boolean) =>
  convertInString(
    header.main,
    ...activeColor('background-3', 'boxShadow-2'),
    screenSize && header.main__close
  );
const btnBurger = convertInString(header.main__btn, 'active-color-1');
const burger = (active: boolean) =>
  convertInString(
    header['main__btn-burger'],
    active && header['main__btn-burger-active']
  );
const link = (value: string, active: string, screenSize: boolean) =>
  convertInString(
    header.link,
    'active-borderColor-1',
    !screenSize && 'active-background-focus',
    ...(value === active
      ? [header.link__active, !screenSize && 'active-background-2']
      : [])
  );
const linkDesc = convertInString(header.link__desc, 'active-color-effect');

const style = { modal, main, btnBurger, burger, link, linkDesc };
export default style;
