import React, { useContext, useEffect, useRef } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Components
import Icon from '../../Common/Icon';
// Assets
import cv from '../../../assets/image/cv';

const BtnCV: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);
  // React Ref
  const btnCVRef = useRef<HTMLButtonElement>(null);
  const boxCVRef = useRef<HTMLDivElement>(null);
  // Ref elements
  const elboxCV = boxCVRef.current;

  useEffect(() => {
    const handleClick = (e: MouseEvent) => {
      const checkEl = (el: Node | null): boolean =>
        !el?.contains(e.target as Node);

      if (checkEl(btnCVRef.current) && checkEl(elboxCV))
        elboxCV?.classList.remove('box__linkCV-active');
    };

    document.body.addEventListener('click', handleClick);

    return () => {
      document.body.removeEventListener('click', handleClick);
    };
  });

  return (
    <div className="box__btnLinkCV">
      <button
        ref={btnCVRef}
        className="btn-CV active-color-1"
        aria-label={lang.bool ? 'Резюме' : 'Resume'}
        onClick={() => elboxCV?.classList.toggle('box__linkCV-active')}
      >
        CV
        <Icon
          active="cv"
          size={{ width: 47, height: 60 }}
          cssClass={{
            iconColor: 'active-fill-1',
            backgroundColor: 'active-fill-2',
          }}
        />
      </button>
      <div
        ref={boxCVRef}
        className="box__linkCV active-background-1 active-boxShadow-2"
      >
        {['en' as const, 'ru' as const].map((language) => (
          <a
            key={`cv-lang-${language}`}
            className="link-CV active-color-2"
            href={cv[language]}
            target="blank"
            onClick={() => elboxCV?.classList.remove('box__linkCV-active')}
            aria-label={lang.bool ? 'Язык резюме' : 'Resume language'}
            children={language}
          />
        ))}
      </div>
    </div>
  );
};

export default BtnCV;
