import React, { useContext, useLayoutEffect, useState } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Utils
import { convertInString } from '../../../utils/js/сonvert';
// Styles-module
import greetingDesc from './greetingDesc.module.scss';

const GreetingDesc: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);
  // React State
  const [tagline, setTagline] = useState<boolean>(false);
  // React LayoutEffect
  useLayoutEffect(() => {
    setTimeout(() => setTagline(true), 800);
  }, []);

  return (
    <p
      className={convertInString(
        greetingDesc.desc,
        tagline && greetingDesc.desc__active
      )}
      dangerouslySetInnerHTML={{
        __html: (lang.bool
          ? 'Галактика нуждается в креативном разработчике...'
          : 'The galaxy needs a creative developer...'
        )
          .split('')
          .map(
            (el, i) =>
              `<span class="active-color-effect" style="transition-delay: ${
                i / 100
              }s">${el}</span>`
          )
          .join(''),
      }}
    />
  );
};

export default GreetingDesc;
