import React, { Suspense, useContext, useEffect, useRef } from 'react';
// Router
import { useLocation } from 'react-router-dom';
// React Context
import { sectionContext } from '../../context/sectionContext';
// Utils
import { convertInString } from '../../utils/js/сonvert';
import { activeColor } from '../../utils/ts/activeColor';
// Components
import About from '../Page/About';
import Loading from './Loading';
// Components -- Lazy
const Abilities = React.lazy(() => import('../Page/Abilities'));
const Education = React.lazy(() => import('../Page/Education'));
const Projects = React.lazy(() => import('../Page/Projects'));
const Contacts = React.lazy(() => import('../Page/Contacts'));
// Styles
const stTitle = convertInString(
  'title',
  ...activeColor('borderColor-1', 'color', 'color-effect')
);

const Main: React.FC = () => {
  // Router
  const search = useLocation().search;
  // React Context
  const {
    lang,
    location: { path },
    sections,
  } = useContext(sectionContext);
  // React Ref
  const titleRef = useRef<HTMLHeadingElement>(null);
  // React Effect
  useEffect(() => {
    const title = titleRef.current;

    if (title) {
      title.classList.add('title__active');
      setTimeout(() => title?.classList.remove('title__active'), 1000);
      import('../../utils/js/smoothScroll').then((mod) =>
        mod.default(title, 100)
      );
    }
  }, [search]);
  // Get active section
  const component = (
    value: string,
    element: React.ReactElement,
    lazy: boolean = false
  ) =>
    value === path ? (
      lazy ? (
        <Suspense fallback={<Loading />} children={element} />
      ) : (
        element
      )
    ) : null;

  return (
    <main>
      <section className="section">
        <h1
          ref={titleRef}
          className={stTitle}
          children={sections[path][lang.code]}
        />
        {component('about', <About />) ||
          component('abilities', <Abilities />, true) ||
          component('education', <Education />, true) ||
          component('projects', <Projects />, true) ||
          component('contacts', <Contacts />, true)}
      </section>
    </main>
  );
};

export default Main;
