import React, { useContext, useEffect, useState } from 'react';
// React Context
import { sectionContext } from '../../../context/sectionContext';
// Components
import Icon from '../../Common/Icon';

const BtnScroll: React.FC = () => {
  // React Context
  const { lang } = useContext(sectionContext);
  // React State
  const [visScrollBtn, setVisScrollBtn] = useState<boolean>(false);
  useEffect(() => {
    // Active smooth scroll up by click button
    const visibleBtn = () =>
      window.pageYOffset > 150 ? setVisScrollBtn(true) : setVisScrollBtn(false);

    window.addEventListener('load', visibleBtn, {
      passive: true,
    });
    window.addEventListener('scroll', visibleBtn, {
      passive: true,
    });

    return () => {
      window.removeEventListener('scroll', visibleBtn);
    };
  }, []);

  return (
    <button
      className="btn-scroll"
      style={{ display: visScrollBtn ? 'block' : 'none' }}
      onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })}
      aria-label={lang.bool ? 'Вверх' : 'Up'}
      children={<Icon active="arrow" size={{ width: 17, height: 30 }} />}
    />
  );
};

export default BtnScroll;
